body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Header {
  background-color: #fff1ab;
  color: black;
}

.HeaderTitle {
  font-family: cursive;
  font-weight: bold;
}

.row {
  margin-left: 10px;
  margin-right: 0px;
}

.DestinationDirectory {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.DestinationDirectory .card {
  margin: 10px 10px;
  width: 18rem;
}

.PanelControls {
  padding: 10px;
}

.PanelControlsLabel {
  font-weight: bold;
}

.PanelControls div {
  padding: 5px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.CenterComponent {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.Home {
  padding-bottom: 2.5rem;
}

.SuccessPage {
  padding: 5px;
  padding-bottom: 2.5rem;
}

.success {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
}

.successGreenColor {
  color: green;
}

.FailurePage {
  padding: 5px;
  padding-bottom: 2.5rem;
}

.failed {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
}

.failedTitleColor {
  color: orange;
}

.ErrorPage {
  padding: 5px;
  padding-bottom: 2.5rem;
}

.error {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  font-size: 25px;
  font-weight: bold;
  color: red;
  text-align: center;
}

.StartAgain {
  padding: 10px;
}

.StartAgain div {
  padding: 5px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}

.Footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
  display: table;
  text-align: center;
  font-weight: bold;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#f3e8ae),
    color-stop(#f1e6b1),
    color-stop(#ede4b7),
    color-stop(#efe5ba),
    to(#f7ecb4)
  );
  background: linear-gradient(
    to bottom,
    #f3e8ae,
    #f1e6b1,
    #ede4b7,
    #efe5ba,
    #f7ecb4
  );
}

.Footer div {
  display: table-cell;
  vertical-align: middle;
}

.App {
  /* background: linear-gradient(to bottom, #fff1ab, #fff4c0, #fff9dd, #fff, #fff); */
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(#fff1ab),
    color-stop(#fff4c0),
    color-stop(#ede5bf),
    color-stop(#f5f0d8),
    to(#f3efdc)
  );
  background: linear-gradient(
    to bottom,
    #fff1ab,
    #fff4c0,
    #ede5bf,
    #f5f0d8,
    #f3efdc
  );
  height: 100%;
  min-height: 100vh;
  position: relative;
}

